.empty {
  width: 100%;
  margin-top: 3rem;

  display: flex;
  justify-content: center;
  align-items: center;
}

.empty h2 {
  font-size: 1.25rem;
  color: #f9d3b4;
  font-family: var(--font-mukta);
}

.container {
  /* width: 50%; */
  margin-top: 3rem;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.game {
  width: 310px;
  height: 460px;
  margin: 1.5rem;
  font-size: 16px;

  position: relative;
  border-radius: 12px;
  overflow: hidden;
  border: none;

  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
}

.game div:nth-of-type(1) {
  position: absolute;
  padding: 16px;
  width: 100%;
  opacity: 0;
  top: 0;
  color: white;
}

.game:hover {
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.05, 1.05);
}

.game div:nth-of-type(2) {
  width: 100%;
  height: 100%;
}

.game img {
  height: 60%;
  width: 100%;
}

.game div:nth-of-type(3) {
  z-index: 2;
  background-color: #343739;
  padding: 16px 24px 24px 24px;
  height: 40%;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
}

.game div:nth-of-type(3) span {
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #f0f0f0;
  
}

.game div:nth-of-type(3) h3 {
  margin-top: 5px;
  font-family: "Mukta", serif;
  color: white;
}


.game:hover div:nth-of-type(2) {
  /* height: 100%; */
  opacity: 0.3;
  cursor: pointer;
}

.game:hover div:nth-of-type(3) {
  background-color: transparent;
  cursor: pointer;
}

.game:hover div:nth-of-type(1) {
  opacity: 1;
  cursor: pointer;
}



.game-info{
  width: 410px;
  height: 610px;
  margin: 1.5rem;
  font-size: 16px;
  color: white;
  position: relative;
  /* border-radius: 12px; */
  overflow: hidden;
  border: none;

  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
  
}

.game-info img {
  height: 256px;
  width: 100%;
}

.game-info-button{
  font-size: 28px;
  width: fit-content;
}

.game-info-button:hover{
  cursor: pointer;
  
}

.game-info-list{
  padding-bottom: 20%;
  overflow-y: scroll;
  height: 200px;

}

.game-info-description{
  font-size: 16px;
  padding-left: 5%;
  padding-right: 5%;
  
}

.show-description{
  text-decoration-line:underline;
  cursor:pointer;
  font-size: 18px;
  color: lightblue;
  width: fit-content;
  
}

.show-description:hover{
  
  color: rgb(84, 126, 139);
}

.platform-list:hover{
  cursor: default;
}

.filter-button{
  /* display: block;
  margin-top: 50%; */
  margin-left: 5%;
  
  width: 24px;
 
  
}

.filter-button:hover{
  cursor: pointer;
}

.filters{
  width: 50%;
  margin: 4rem 0 2rem;

  display: flex;
 

  padding: 1.5rem 1.75rem;
  border-radius: 3rem;
  color: white;
  background: #1f2123;
  -webkit-box-shadow: 5px 5px 7px #1c1d1f, -5px -5px 7px #222527;
  box-shadow: 5px 5px 7px #1c1d1f, -5px -5px 7px #222527;
  
}


.arrow {
  border: solid white;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
  width: 20px;
  height: 20px;
  margin-top: 5%;
}

.arrow:hover {
  cursor: pointer;
}

.up {
  transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
}

.down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

.footer{
  /* height: 256px;
  width: 100vw;
  background-color: #343739; */

  margin-top: 4%;
  justify-content: center;
  color: white;
  font-size: 12px;
  
  
}

a:visited { text-decoration: none; color:lightblue; }
a:hover { text-decoration: none; color:blue; }
a:focus { text-decoration: none; color:lightblue; }
a:hover, a:active { text-decoration: none; color:darkslateblue }





@media screen and (max-width: 1000px) {
  .app {
    padding: 4rem 2rem;
  }

  .search {
    padding: 1rem 1.75rem;
    width: 100%;
  }

  .search input {
    font-size: 1rem;
  }

  .search img {
    width: 20px;
    height: 20px;
  }

  h1 {
    font-size: 1.5rem;
  }

  .container {
   
    width: 100%;
  }

  .filters{
    width: 80%;
  }
}

@media screen and (max-width: 400px) {
  .app {
    padding: 4rem 1rem;
  }

  h1 {
    font-size: 1.5rem;
  }

  .container {
    margin-top: 2rem;
    width: 100%;
  }

  .movie {
    width: "100%";
    margin: 1rem;
  }

  .filters{
    width: 80%;
  }

  .filter-button{
    width: 12px;
  }
  
}







@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loading-spinner {
  width: 50px;
  height: 50px;
  border: 10px solid #f3f3f3; /* Light grey */
  border-top: 10px solid #383636; /* Black */
  border-radius: 50%;
  animation: spinner 1.5s linear infinite;
}